import React from "react";
import queryString from 'query-string'
import Login from "./Login";
import tokenManager from "../../tokenManager";
import { apiUserFound } from "../../redux/actions";

class LoginContainer extends React.Component {
  constructor() {
    super();
    this.onLoginButtonClick = this.onLoginButtonClick.bind(this);
  }

  async onLoginButtonClick(event) {
    event.preventDefault();
    const auth = await tokenManager.fetchAuth(1234);
    this.props.dispatch(apiUserFound(auth));
    tokenManager.storeApiUser(auth.token);
    this.props.history.push("/")
    //userManager.signinRedirect();
  }

  error() {
    const values = queryString.parse(this.props.location.search)
    return Object.keys(values).indexOf("error") !== -1
  }

  render() {
    return (
      <Login onClick={this.onLoginButtonClick} error={this.error()} />
    );
  }
}

export default LoginContainer;